import React, { useState } from 'react'
import { Box, TextInput, RadioButton, Select, MaskedInput } from 'grommet'

function PostSalaryBenefits(params) {
  const [skill, setSkill] = useState('');

  const [bonus, setBonus] = useState('');

  return (
    <div>
      <h3>Position</h3>
      <Box width='medium'>
        <TextInput value={params.position} onChange={event => params.setPosition(event.target.value)} placeholder='Java developer' />
      </Box>
      <h3>Skills</h3>
      <Box width='medium'>
        <TextInput value={skill} onChange={event => setSkill(event.target.value)} placeholder='Type a skill' />
      </Box>
        Smart suggestions based n other positions like yours<br />
          tags go here
      <h3>Employment Type</h3>
      <Box align='center' direction='row'>
        <Box margin='small'>
          <RadioButton
            label='Permanent'
            name='name'
            value='permanent'
            checked={params.employmentType === 'permanent'}
            onChange={event => params.setEmploymentType(event.target.value)}
          />
        </Box>
        <Box margin='small'>
          <RadioButton
            label='Freelance'
            name='name'
            value='freelance'
            checked={params.employmentType === 'freelance'}
            onChange={event => params.setEmploymentType(event.target.value)}
          />
        </Box>
        <Box margin='small'>
          <RadioButton
            label='Part-time'
            name='name'
            value='parttime'
            checked={params.employmentType === 'parttime'}
            onChange={event => params.setEmploymentType(event.target.value)}
          />
        </Box>
        <Box margin='small'>
          <RadioButton
            label='Internship'
            name='name'
            value='internship'
            checked={params.employmentType === 'internship'}
            onChange={event => params.setEmploymentType(event.target.value)}
          />
        </Box>
      </Box>
      <h3>Salary</h3>
      <Box width='small'>
        <Select
          options={['GBP', 'EUR', 'USD']}
          value={params.currency}
          onChange={({ option }) => params.setCurrency(option)}
          placeholder='Currency'
        />
      </Box>
      <Box align='center' direction='row'>
        <MaskedInput
          mask={[
            {
              regexp: /^[0-9]{1,7}$/,
              placeholder: "50000"
            },
          ]}
          value={params.minSalary}
          onChange={event => params.setMinSalary(event.target.value)}
        />
        <MaskedInput
          mask={[
            {
              regexp: /^[0-9]{1,7}$/,
              placeholder: "60000"
            },
          ]}
          value={params.maxSalary}
          onChange={event => params.setMaxSalary(event.target.value)}
        />
      </Box>
      <h3>Bonus</h3>
      <Box width='medium'>
        <TextInput value={bonus} onChange={event => setBonus(event.target.value)} placeholder='Personal Performance, Stocks...' />
      </Box>
    </div>
  );
}

export default PostSalaryBenefits;